<template>
    <div class="wrapper">
        <img class="wrapper__img" alt="" src="http://www.dell-lee.com/imgs/vue3/user.png">
        <div class="wrapper__input">
          <input class="wrapper__input__content" 
          placeholder="请输入手机号" v-model="username">
        </div>
        <div class="wrapper__input">
          <input class="wrapper__input__content" placeholder="请输入密码" 
          type="password" autocomplete="new-password" v-model="password">
        </div>
        <div class="wrapper__input">
          <input class="wrapper__input__content" placeholder="请确认密码" 
          type="password" autocomplete="new-password" v-model="password">
        </div>
        <div class="wrapper__register-button" @click=" handrreclick">注册</div>
        <div class="wrapper__register-link" @click="handrclick">已有账号</div>
        <toast v-if="show" :message = "message"/>
    </div>
</template>
<script>
    import { useRouter } from 'vue-router';
    import { reactive,toRefs } from 'vue';
    import {post} from '../../utils/request'
    import toast ,{usetoasteffect}from '../../components/toast.vue'
    const useregistereffect = (showtoast) =>{
    const router = useRouter();
    const data = reactive({
            username :'',
            password: '',
          ensurement:''
        })
    const handrreclick =async ()  =>{
            try{
                const result = await post('https://www.fastmock.site/mock/ae8e9031947a302fed5f92425995aa19/jd/api/user/register',{
                usertName: data.username,
                password: data.password
})
           
  if(result?.errno ===0){
    router.push({name:'login'})
  }else{
    showtoast('注册失败')
    // alert('失败')
  }
}catch(e){
    showtoast('请求失败')
}
}
const {username,password,ensurement} = toRefs(data)
return {username,password,ensurement,handrreclick}
}
const uselogineffect = () =>{
    const router = useRouter();
    const handrclick =()=>{
        router.push({name:'login'})
        }
        return{handrclick}
}

export default{
    name:'register',
    components:{toast},
    setup(){
       
        const{show,toastmessage,showtoast} = usetoasteffect()
        const{username,password,ensurement,handrreclick}= useregistereffect(showtoast)
       const {handrclick} = uselogineffect
        return {username,password,ensurement,handrreclick,handrclick,show,toastmessage}

    }
}
</script>
<style lang="scss" scoped>
.wrapper{
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    &__img{
        display: block;
        margin: 0 auto .4rem auto;
        width: 0.66rem;
        height: 0.66rem;
    }
    &__input{
        padding: 0 .16rem;
        height: .48rem;
        margin: 0 .4rem .16rem .4rem;
        background: #f9f9f9;
        border: .01rem solid navajowhite;
        border-radius:.06rem;
        border-radius:.06rem ;
        &__content{
            border: none;
            outline: none;
            line-height: .22rem;
            margin-top: .12rem;
            width: 100%;
            background: none;
            font-size: .16rem;
            color:rgba(0,0,0,0.5) ;
            &::placeholder{
                color: rgba(0,0,0,0.5);
            }
        }
    }
    &__register-button{
        margin: .32rem .4rem .16rem .4rem;
        line-height: .48rem;
        background: #0091ff;
        box-shadow: 0 .04rem .08rem 0 rgba(0,145,255,0.32);
        border-radius: .04rem;
        border-radius: .04rem;
        color: #fff;
        font-size: .16rem;
        text-align: center;
    }
    &__register-link{
        font-size: .14rem;
        text-align: center;
        color: rgba(0,0,0,0.5);
    }
}
</style>